import React from "react";
import {
  DesktopHome,
  Widget,
  WebPageLogin,
  PrivateRoute,
  PublicRoute,
} from "@workfolio/shared";
import { Route, Link, Redirect } from "react-router-dom";

export function Routes(props) {
  const {
    // user,
    viewMode,
    setViewMode,
    handleFullScreen,
    currentWorkingTask,
    appMaintenanceStatus,
    currentTeamId
  } = props;

  const enhancedRouteProps = {
    route: Route,
    link: Link,
    redirect: Redirect,
    from: "desktop",
  };

  const Overview = Widget;

  return (
    <>
      <Route exact path="/">
        <Redirect to="/home" />
      </Route>

      <PublicRoute
        exact
        path="/login"
        component={WebPageLogin}
        {...enhancedRouteProps}
        from="desktop"
      />

      <PrivateRoute
        exact
        path="/home"
        component={DesktopHome}
        viewMode={viewMode}
        setViewMode={setViewMode}
        handleFullScreen={handleFullScreen}
        appMaintenanceStatus={appMaintenanceStatus}
        currentWorkingTask={currentWorkingTask}
        currentTeamId={currentTeamId}
        {...enhancedRouteProps}
      />

      <PrivateRoute
        exact
        path={["/overview"]}
        component={Overview}
        viewMode={viewMode}
        setViewMode={setViewMode}
        handleFullScreen={handleFullScreen}
        currentWorkingTask={currentWorkingTask}
        currentTeamId={currentTeamId}
        {...enhancedRouteProps}
      />
    </>
  );
}
