import { gql } from "./apollo";

export const GET_ORGANISATION_SETTINGS_QUERY = gql`
  query GetOrganisationSettings($organisationId: String!) {
    getOrganisationSettings(organisationId: $organisationId) {
      featureName
      featureLabel
      value
      valueType
      unit
    }
  }
`;

export const CURRENT_WORKING_TASK_QUERY = gql`
  query CurrentWorkingTask($organisationId: String!) {
    currentWorkingTask(organisationId: $organisationId) {
      teamId
      organisationId
      userId
      updatedAt
      workedSecs
      untrackedSeconds
      taskTimer: workedSecs
      activityType
      taskId
      taskName
      projectId
      projectName
      taskDescription
      taskTimeId
      taskStartTime
    }
  }
`;

export const APP_DETAILS_QUERY = gql`
  query GetAppVersionDetails($organisationId: String!, $appEnvironment: String!, $appVersion: String!) {
    getAppVersionDetails(organisationId: $organisationId, appEnvironment: $appEnvironment, appVersion: $appVersion) {
      appVersion
      updateApp
      appMaintenanceStatus
    }
  }
`;