import { useMutation, useQuery } from "./apollo";
import React, { useEffect, useCallback, useState } from "react";
import "./App.css";
import { AppHeader, UserContext, TrackingContext } from "@workfolio/shared";
import Spinner from "@workfolio/shared/node_modules/react-bootstrap/Spinner";
import {
  GET_ORGANISATION_SETTINGS_QUERY,
  CURRENT_WORKING_TASK_QUERY,
  APP_DETAILS_QUERY,
} from "./query";
import { HashRouter } from "react-router-dom";
import { Routes } from "./routes";
import jwtDecode from "jwt-decode";

const localAppVersion = window?.navigator?.appVersion
  .split("Workfolio/")
  .pop()
  .split(" ")[0];

const toCamel = (s) => {
  return s.replace(/([-_][a-z])/gi, ($1) => {
    return $1.toUpperCase().replace("-", "").replace("_", "");
  });
};

const getTask = async () => {
  if (!window.ipcRenderer) return;
  const currentTask = await window.ipcRenderer.invoke(
    "get_store_value",
    "currentTask"
  );
  return currentTask;
};

const handleClose = () => {
  if (!window.ipcRenderer) return;
  window.ipcRenderer.send("window_close");
};

const handleMinimize = () => {
  if (!window.remote) return;
  const win = window.remote.getCurrentWindow();
  win.minimize();
};

const handleFullScreen = () => {
  if (!window.remote) return;
  const appLoader = document.querySelector("#app-loader");
  if (appLoader) {
    appLoader.style.display = "flex";
  }
  setTimeout(() => {
    const win = window.remote.getCurrentWindow();
    win.setFullScreen(!win.isFullScreen());
    const appLoader = document.querySelector("#app-loader");
    if (appLoader) {
      appLoader.style.display = "none";
    }
  }, 200);
};

const handleStart = async (data = {}) => {
  if (!window.ipcRenderer) {
    return;
  }
  const startResonse = await window.ipcRenderer.invoke("start_tracking", data);
  console.log("started", startResonse);
};

const handleStartTask = async (data = {}) => {
  if (!window.ipcRenderer) return;
  const { ipcRenderer } = window;
  const startResonse = await ipcRenderer.invoke("start_task", data);
  console.log("started a task", startResonse);
};

const handleStopTask = async () => {
  if (!window.ipcRenderer) return;
  const { ipcRenderer } = window;
  const stopResonse = await ipcRenderer.invoke("start_task", {});
  console.log("stopped a task", stopResonse);
};

const handleStop = async (activityType = "stop", activityReason) => {
  if (!window.ipcRenderer) return;
  const { ipcRenderer } = window;
  const stopResonse = await ipcRenderer.invoke(
    "stop_tracking",
    activityType,
    activityReason
  );
  console.log("stopped", stopResonse);
};

const updateOnlineStatus = () => {
  if (!window.ipcRenderer) return;
  const { ipcRenderer } = window;
  const networkStatus = navigator.onLine ? "online" : "offline";
  ipcRenderer.send("online_status_changed", networkStatus);
  ipcRenderer.invoke("set_store_value", "internetStatus", navigator.onLine);
};

const requestPermission = () => {
  if (!window.ipcRenderer) return;
  const { ipcRenderer } = window;
  ipcRenderer.send("request_permission");
};

const AppContainer = () => {
  const [isLoaded, setLoaded] = useState(false);
  const [hasPermission, setPermission] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [task, setTask] = useState(null);
  const [currentTeamId, setCurrentTeamId] = useState(null);

  const token = localStorage.getItem("token");
  const [user, setUser] = useState(token ? jwtDecode(token) : null);

  // console.log("User Data", user);

  if (!token) {
    const { ipcRenderer } = window;
    ipcRenderer.invoke("reset_store_value");
  }

  useEffect(() => {
    if (window.ipcRenderer) {
      window.ipcRenderer.on("permission", handlePermission);
      window.ipcRenderer.on("refresh", handleRefresh);
    }

    // updateOnlineStatus();

    window.addEventListener("online", updateOnlineStatus);
    window.addEventListener("offline", updateOnlineStatus);

    return () => {
      window.removeEventListener("online", updateOnlineStatus);
      window.removeEventListener("offline", updateOnlineStatus);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    console.log("Internet Connection", navigator.onLine);
    updateOnlineStatus();
  }, [navigator.onLine]);

  useEffect(() => {
    async function restoreUserData() {
      if (!window.ipcRenderer) return;

      const userToken = await window.ipcRenderer.invoke(
        "get_store_value",
        "userToken"
      );
      const currentTask = await window.ipcRenderer.invoke(
        "get_store_value",
        "currentTask"
      );
      const currentTeamId = await window.ipcRenderer.invoke(
        "get_store_value",
        "currentTeamId"
      );
      if (!userToken) {
        localStorage.removeItem("token");
      } else {
        localStorage.setItem("token", userToken);
        setUser(typeof userToken === "string" ? jwtDecode(userToken) : null);
      }

      if (!currentTask) {
        setTask(null);
      }
      if (currentTeamId) {
        setCurrentTeamId(currentTeamId);
      }
      setLoaded(true);
    }

    restoreUserData();
  }, []);

  useEffect(() => {
    async function setServerToken() {
      if (!window.ipcRenderer) return;

      const userToken = localStorage.getItem("token");

      await window.ipcRenderer.invoke(
        "set_store_value",
        "userToken",
        userToken
      );

      // setTimeout(() => {
      window.ipcRenderer.send("user_login_success");
      // }, 1000);
    }

    if (!isLoaded) return;
    setServerToken();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [user]);

  const onStart = async (data = {}) => {
    await handleStart(data);
    setTask({ ...data, updatedAt: new Date(), activityType: "start" });
  };

  const onStop = async (activityType = "stop", activityReason) => {
    await handleStop(activityType, activityReason);
    if (activityType === "stop") {
      setTask(null);
    } else {
      setTask((prev) => {
        return {
          ...prev,
          updatedAt: new Date(),
          activityType,
        };
      });
    }
  };

  const onStartTask = async (data = {}) => {
    await handleStartTask(data);
    setTask((prev) => ({
      ...prev,
      ...data,
      updatedAt: new Date(),
    }));
  };

  const onStopTask = async () => {
    await handleStopTask();
    setTask((prev) => {
      const { taskId, projectId, ...rest } = prev;
      return {
        ...rest,
        updatedAt: new Date(),
      };
    });
  };

  const handleRefresh = useCallback(async () => {
    const currentTask = await getTask();
    setTask(currentTask);
    setRefresh(true);
  }, []);

  const handlePermission = useCallback((_event, isPermitted) => {
    setPermission(isPermitted);
  }, []);

  const organisationId = user?.organisations?.[0]?.id;

  function getOperatingSystem(window) {
    let operatingSystem = "not_known";

    if (window.navigator.appVersion.indexOf("Win") !== -1) {
      operatingSystem = "windows";
    }
    if (window.navigator.appVersion.indexOf("Mac") !== -1) {
      operatingSystem = "mac";
    }
    if (window.navigator.appVersion.indexOf("X11") !== -1) {
      operatingSystem = "unix";
    }
    if (window.navigator.appVersion.indexOf("Linux") !== -1) {
      operatingSystem = "linux";
    }

    return operatingSystem;
  }

  const appEnvironment = getOperatingSystem(window);

  const {
    data: currentAppVersionData,
    error: currentAppVersionError,
    loading: currentAppVersionLoading,
    refetch: currentAppVersionRefetch,
  } = useQuery(APP_DETAILS_QUERY, {
    variables: { organisationId, appEnvironment, appVersion: localAppVersion },
    skip: !organisationId,
    pollInterval: 10800000,
  });

  const {
    data: settings,
    error: settingsError,
    loading: settingLoading,
    refetch: refreshSetting,
  } = useQuery(GET_ORGANISATION_SETTINGS_QUERY, {
    variables: { organisationId },
    skip: !organisationId,
    pollInterval: 3600000,
  });

  const {
    data: currentTask,
    error: currentTaskError,
    loading: currentTaskLoading,
    refetch: refreshCurrentTask,
  } = useQuery(CURRENT_WORKING_TASK_QUERY, {
    variables: { organisationId },
    skip: !organisationId,
    fetchPolicy: "cache-and-network",
  });

  useEffect(() => {
    currentAppVersionRefetch();
    refreshSetting();
    refreshCurrentTask();
  }, [navigator.onLine]);

  useEffect(() => {
    if (!refresh) return;
    if (refreshCurrentTask) refreshCurrentTask();
    if (refreshSetting) refreshSetting();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [refresh]);

  useEffect(() => {
    console.log("Refteching settings");
    if (settingsError || settingLoading) return;

    let orgSettings = settings?.getOrganisationSettings;
    const { ipcRenderer } = window;

    if (!orgSettings || !ipcRenderer) return;

    orgSettings = orgSettings.reduce((acc, curr) => {
      let val = curr.value;
      if (curr.valueType === "boolean") {
        val = curr.value === "true";
      }
      if (curr.valueType === "number") {
        val = Number(curr.value);
      }
      acc[toCamel(curr.featureName)] = val;
      return acc;
    }, {});

    ipcRenderer.invoke("set_store_value", {
      organisationSettings: orgSettings,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [settings]);

  useEffect(() => {
    if (currentTaskLoading) return;
    const { ipcRenderer } = window;

    const currentWorkingTask = currentTask?.currentWorkingTask;

    if (!currentWorkingTask) {
      // ipcRenderer.invoke("delete_store_value", "currentTask");
      setTask(null);
    } else {
      ipcRenderer.invoke("set_store_value", {
        currentTask: currentWorkingTask,
      });
      setTask(currentWorkingTask);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentTask]);

  useEffect(() => {
    if (currentAppVersionLoading) return;

    const localAppVersion = window.navigator.appVersion
      .split("Workfolio/")
      .pop()
      .split(" ")[0];

    const { ipcRenderer } = window;
    ipcRenderer.invoke("set_store_value", {
      version: localAppVersion,
    });

    if (currentAppVersionData) {
      const {
        appVersion,
        updateApp,
        // appMaintenanceStatus = false,
      } = currentAppVersionData?.getAppVersionDetails;

      if (appVersion === localAppVersion) {
        console.log("You have the lastest version of workfolio.");
      } else if (appVersion !== localAppVersion && !updateApp) {
        console.log("You are not eligible for the latest update.");
      } else {
        const { ipcRenderer } = window;
        ipcRenderer.send("check_for_app_updates");
      }
    }
  }, [currentAppVersionData]);

  const organisationSettings =
    window.ipcRenderer.invoke("get_store_value", "organisationSettings") ||
    null;
  // console.log(organisationSettings);
  const previousProjectDetails =
    window.ipcRenderer.invoke("get_store_value", "previousProjectDetails") ||
    null;
  // console.log(previousProjectDetails);

  const error = settingsError || currentTaskError || currentAppVersionError;
  const loading = !isLoaded || settingLoading || currentTaskLoading;
  const currentWorkingTask = currentTask?.currentWorkingTask;

  let appMaintenanceStatus =
    currentAppVersionData?.getAppVersionDetails?.appMaintenanceStatus || false;

  // if (currentAppVersionError) {
  //   currentAppVersionRefetch();
  // }

  // if (settingsError) {
  //   refreshSetting();
  // }

  // if (currentTaskError) {
  //   refreshCurrentTask();
  // }

  return (
    <TrackingContext.Provider
      value={{
        task,
        onStop,
        onStart,
        refresh,
        onStopTask,
        setRefresh,
        onStartTask,
        hasPermission,
        requestPermission,
      }}
    >
      <UserContext.Provider value={{ user, setUser }}>
        <HashRouter>
          <App
            loading={loading}
            error={error}
            refreshCurrentTask={refreshCurrentTask}
            currentAppVersionRefetch={currentAppVersionRefetch}
            refreshSetting={refreshSetting}
            currentWorkingTask={currentWorkingTask}
            appMaintenanceStatus={appMaintenanceStatus}
            user={user}
            organisationSettings={organisationSettings}
            previousProjectDetails={previousProjectDetails}
            currentTeamId={currentTeamId}
          />
        </HashRouter>
      </UserContext.Provider>
    </TrackingContext.Provider>
  );
};

export default AppContainer;

const App = (props) => {
  const {
    error,
    loading,
    refreshCurrentTask,
    refreshSetting,
    currentAppVersionRefetch,
    currentWorkingTask,
    appMaintenanceStatus,
    user,
    currentTeamId,
  } = props;
  const [viewMode, setViewMode] = useState("widget");

  const handleWindowResize = useCallback((_event, size) => {
    refreshCurrentTask();
    const { width } = size;
    if (width <= 800) {
      setViewMode("widget");
    } else {
      setViewMode("fullScreen");
    }
  }, []);

  useEffect(() => {
    // if(!user) return;
    if (!window.ipcRenderer) return;

    window.ipcRenderer.on("window_size_changed", handleWindowResize);
    window.ipcRenderer.on("refresh_current_task", refreshCurrentTask);
    window.ipcRenderer.on("refresh_settings", refreshSetting);
    window.ipcRenderer.on("refresh_app_version", currentAppVersionRefetch);
    window.ipcRenderer.send("get_window_size");

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!navigator.onLine && user) {
    const sendConsoleEvent = window?.ipcRenderer?.send(
      "send_console_event",
      !navigator.onLine
        ? "Network Issue / No Internet Logged"
        : "Query Error logged"
    );
    return (
      <div id="app-root">
        <AppHeader
          handleMinimize={handleMinimize}
          handleClose={handleClose}
          // handleFullScreen={handleFullScreen}
        />
        <div style={{ display: "grid", placeItems: "center", height: "90vh" }}>
          <div>
            <img
              src={require("./under_maintenance.png")}
              style={
                window.innerHeight > 1000
                  ? { width: "100%" }
                  : { width: "77%", marginLeft: "12%" }
              }
            />
            <p style={{ textAlign: "center" }}>
              {" "}
              <b>We are unable to reach our servers.</b> <br></br>Check your
              internet connection and try again.<br></br>
              <br></br>
              <a
                onClick={() => window.ipcRenderer.send("reload_application")}
                style={{ cursor: "pointer" }}
              >
                <b>Click here</b>
              </a>{" "}
              to reload Workfolio. <br></br>
              <span style={{ color: "red" }}>
                To check Workfolio's Server status{" "}
                <a
                  onClick={() =>
                    window.ipcRenderer.send(
                      "open_external_link",
                      `https://workfolio.statuspage.io/`
                    )
                  }
                  style={{ cursor: "pointer" }}
                >
                  <b>click here.</b>
                </a>
              </span>{" "}
              <br></br>
              <br></br>If this issue persists,<br></br>
              Click on the need help button at the bottom.
            </p>
          </div>
        </div>
        <p
          style={{
            top: 40,
            right: 20,
            position: "fixed",
            cursor: "pointer",
            textDecoration: "underline",
          }}
          onClick={() => window.ipcRenderer.send("reset_sign_out")}
        >
          {" "}
          Sign out
        </p>
        <img
          src={require("./need_help.png")}
          style={{
            bottom: 20,
            right: 20,
            position: "fixed",
            cursor: "pointer",
          }}
          onClick={() => window.ipcRenderer.send("open_chat_support")}
        />
      </div>
    );
  }

  if (appMaintenanceStatus === true && user) {
    const sendConsoleEvent = window?.ipcRenderer?.send(
      "send_console_event",
      "App Maintenance Logged"
    );
    const maintenanceStatus = window?.ipcRenderer?.send("focus_app_window");

    return (
      <div id="app-root">
        <AppHeader
          handleMinimize={handleMinimize}
          handleClose={handleClose}
          // handleFullScreen={handleFullScreen}
        />
        <div style={{ display: "grid", placeItems: "center", height: "90vh" }}>
          <div>
            <img
              src={require("./under_maintenance.png")}
              style={{ width: "100%" }}
            />
            <p style={{ textAlign: "center" }}>
              {" "}
              <b>Workfolio is currently down for maintenance.</b> <br></br>We
              expect to be back in a couple of hours. <br></br>Thanks for your
              patience.
            </p>
          </div>
        </div>
        <img
          src={require("./need_help.png")}
          style={{
            bottom: 20,
            right: 20,
            position: "fixed",
            cursor: "pointer",
          }}
          onClick={() => window.ipcRenderer.send("open_chat_support")}
        />
      </div>
    );
  }

  return (
    <div id="app-root">
      <AppHeader
        handleMinimize={handleMinimize}
        handleClose={handleClose}
        handleFullScreen={handleFullScreen}
      />
      {/* {loading ? <Loading /> : null} */}
      {/* {error ? <Error /> : null} */}
      {!error && !loading && currentWorkingTask ? (
        <div id="app-loader">
          <Spinner animation="border" role="status" variant="secondary">
            <span className="sr-only">Loading...</span>
          </Spinner>
        </div>
      ) : null}
      <Routes
        user={user}
        viewMode={viewMode}
        setViewMode={setViewMode}
        handleFullScreen={handleFullScreen}
        currentWorkingTask={currentWorkingTask}
        appMaintenanceStatus={appMaintenanceStatus}
        currentTeamId={currentTeamId}
      />
      <img
        src={require("./need_help.png")}
        style={{ bottom: 20, right: 20, position: "fixed", cursor: "pointer" }}
        onClick={() => window.ipcRenderer.send("open_chat_support")}
      />
    </div>
  );
};

const Loading = () => {
  return (
    <div id="app-loader" style={{ display: "flex" }}>
      <Spinner animation="border" role="status" variant="primary">
        <span className="sr-only">Loading...</span>
      </Spinner>
    </div>
  );
};
